.Venus .slick-dots {
  margin: -5px 0;
  font-size: 0;
  text-align: center;
}

.Venus .slick-dots li {
  display: inline-block;
  vertical-align: top;
}

.Venus .slick-dots li.slick-active button:after {
  opacity: 1;
}

.Venus .slick-dots li button {
  position: relative;
  width: 20px;
  height: 20px;
  background: none;
  font-size: 0;
}

.Venus .slick-dots li button:before, .Venus .slick-dots li button:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border: 1px solid #50E3C2;
  border-radius: 50%;
}

.Venus .slick-dots li button:after {
  opacity: 0;
  background: #50E3C2;
  transition: opacity 0.25s;
}

.Venus [data-accordion-main] [data-accordion-btn].is-active .icon-down {
  transform: translate3d(0, -50%, 0) rotate(180deg);
}

.Venus [data-accordion-main] [data-accordion-btn] .icon-down {
  position: absolute;
  top: 50%;
  right: 0;
  font-size: 6px;
  fill: currentColor;
  transform: translate3d(0, -50%, 0);
  transition: transform .25s;
}

@media only screen and (max-width: 767px) {
  .Venus [data-accordion-main*="phone"] [data-accordion-btn] {
    cursor: pointer;
  }
}

.Venus [data-accordion-main*="phone"] [data-accordion-btn] .icon-down {
  display: none;
}

@media only screen and (max-width: 767px) {
  .Venus [data-accordion-main*="phone"] [data-accordion-btn] .icon-down {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .Venus [data-accordion-main*="phone"] [data-accordion-container] {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .Venus [data-accordion-main*="phone"] [data-accordion-container].is-active {
    display: block;
  }
}

.Venus [data-tabs] [data-tabs-control] {
