.Venus .v-autocomplete {
  position: absolute;
  display: none;
  overflow: auto;
  padding: 5px 0 10px;
  max-height: 150px;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 7px 19px 2px rgba(0, 0, 0, 0.05);
}

.Venus .v-autocomplete__item {
  position: relative;
  z-index: 1;
  padding: 5px 12px 5px 23px;
  border-radius: 4px;
  font-size: 14px;
  line-height: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.175s, background 0.175s;
}

.is-notouch .Venus .v-autocomplete__item:hover {
  color: #fff;
  background: #50E3C2;
}

.is-notouch .Venus .v-autocomplete__item:hover .icon {
  fill: #fff;
}

.Venus .v-autocomplete__item .icon {
  position: absolute;
  top: 50%;
  left: 6px;
  margin-top: -8px;
  fill: #5B6D7C;
  font-size: 16px;
  transition: fill 0.175s;
}

.Venus .v-scrollbar {
  position: relative;
  overflow: hidden !important;
}

.Venus .v-scrollbar.ps--active-y {
  padding-right: 26px;
}

.Venus .v-scrollbar.ps--active-y .ps__rail-y {
  opacity: 1;
  visibility: visible;
}

.Venus .v-scrollbar .ps__rail-y {
  position: absolute;
  top: 0;
  right: 10px;
  width: 6px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.25s, visibility 0.25s;
}

.Venus .v-scrollbar .ps__rail-y:before {
  content: '';
  position: absolute;
  top: 5px;
  left: 0;
  right: 0;
  bottom: 5px;
  border-radius: 4px;
  background: rgba(207, 222, 229, 0.5);
}

.Venus .v-scrollbar .ps__thumb-y {
  position: absolute;
  left: 0;
  right: 0;
}

.Venus .v-scrollbar .ps__thumb-y:before {
  content: '';
  position: absolute;
  top: 5px;
  left: 0;
  right: 0;
  bottom: 5px;
  background: #CFDEE5;
  border-radius: 4px;
}

